import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import Header from "../../components/feature/Header";
import { useAuth } from "../../contexts/AuthContext";
import {
  getMatchingRooms,
  createMatchingRoom,
  joinMatchingRoom,
  leaveMatchingRoom,
  startMatchingRoom,
} from "../../services";
import type { MatchingRoom } from "../../types";

const CAPACITY = 3;

export default function MatchingRoomsPage() {
  const navigate = useNavigate();
  const { updateUserTeam, user } = useAuth();
  const [rooms, setRooms] = useState<MatchingRoom[]>([]);
  const [loading, setLoading] = useState(true);
  const [actionRoomId, setActionRoomId] = useState<string | null>(null);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [newRoomName, setNewRoomName] = useState("");
  const [creating, setCreating] = useState(false);
  /** 나가기 확인 모달에 표시할 방 id (null이면 모달 비표시) */
  const [leaveConfirmRoomId, setLeaveConfirmRoomId] = useState<string | null>(
    null
  );

  useEffect(() => {
    getMatchingRooms().then((list) => {
      setRooms(list);
      setLoading(false);
    });
  }, []);

  const handleJoin = async (roomId: string) => {
    if (!user) return;
    setActionRoomId(roomId);
    const result = await joinMatchingRoom(roomId, {
      id: user.id,
      nickname: user.nickname,
    });
    setActionRoomId(null);
    if (result.success) {
      getMatchingRooms().then(setRooms);
    } else {
      alert(result.message);
    }
  };

  const handleLeave = async (roomId: string) => {
    setActionRoomId(roomId);
    const result = await leaveMatchingRoom(roomId);
    setActionRoomId(null);
    setLeaveConfirmRoomId(null);
    if (result.success) {
      getMatchingRooms().then(setRooms);
    }
  };

  const openLeaveConfirm = (roomId: string) => setLeaveConfirmRoomId(roomId);
  const closeLeaveConfirm = () => setLeaveConfirmRoomId(null);

  const handleStart = async (roomId: string) => {
    setActionRoomId(roomId);
    const result = await startMatchingRoom(roomId);
    setActionRoomId(null);
    if (result.success) {
      if (result.teamId) updateUserTeam(result.teamId);
      navigate("/mock-investment");
    }
  };

  const handleCreateRoom = async () => {
    setCreating(true);
    const result = await createMatchingRoom(newRoomName.trim() || undefined);
    setCreating(false);
    if (result.success) {
      setShowCreateModal(false);
      setNewRoomName("");
      getMatchingRooms().then(setRooms);
    }
  };

  const isFull = (room: MatchingRoom) => room.memberCount >= CAPACITY;
  const isInRoom = (room: MatchingRoom) =>
    user && room.members.some((m) => m.userId === user.id);

  /** 내가 참가한 방 중 멤버가 부족한 방(2명 이하) — 이 경우 대기 페이지 표시 */
  const myWaitingRoom = rooms.find(
    (room) => isInRoom(room) && room.memberCount < CAPACITY
  );

  return (
    <div className="min-h-screen bg-gray-50">
      <Header />

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* 멤버 부족 시: "팀원을 매칭중입니다..." 전용 페이지 */}
        {!loading && myWaitingRoom ? (
          <div className="flex flex-col items-center justify-center min-h-[60vh] text-center px-4">
            <div className="w-20 h-20 rounded-full bg-teal-100 flex items-center justify-center mb-6">
              <i
                className="ri-user-search-line text-4xl text-teal-600"
                aria-hidden
              />
            </div>
            <h2 className="text-xl font-bold text-gray-900 mb-2">
              팀원을 매칭중입니다...
            </h2>
            <p className="text-gray-500 mb-1">
              <span className="font-medium text-gray-700">
                {myWaitingRoom.name}
              </span>{" "}
              방에서 팀원이 모일 때까지 잠시만 기다려 주세요.
            </p>
            <p className="text-sm text-gray-500 mb-8">
              현재 {myWaitingRoom.memberCount}명 / {myWaitingRoom.capacity}명
            </p>
            <ul className="flex flex-wrap justify-center gap-4 mb-8">
              {myWaitingRoom.members.map((member) => (
                <li key={member.userId} className="flex flex-col items-center">
                  <span className="w-14 h-14 rounded-full bg-teal-500 text-white flex items-center justify-center text-lg font-semibold shrink-0 border-2 border-white shadow" aria-hidden>
                    {member.nickname.charAt(0)}
                  </span>
                  <span className="text-sm font-medium text-gray-700 mt-2 truncate max-w-[100px]">
                    {member.nickname}
                  </span>
                </li>
              ))}
              {Array.from({
                length: myWaitingRoom.capacity - myWaitingRoom.members.length,
              }).map((_, i) => (
                <li key={`empty-${i}`} className="flex flex-col items-center">
                  <div className="w-14 h-14 rounded-full bg-gray-100 border-2 border-dashed border-gray-300 flex items-center justify-center">
                    <i
                      className="ri-user-add-line text-2xl text-gray-400"
                      aria-hidden
                    />
                  </div>
                  <span className="text-sm text-gray-400 mt-2">대기 중</span>
                </li>
              ))}
            </ul>
            <button
              type="button"
              onClick={() => openLeaveConfirm(myWaitingRoom.id)}
              disabled={actionRoomId === myWaitingRoom.id}
              className="py-2.5 px-6 rounded-xl text-sm font-medium border border-gray-300 text-gray-700 hover:bg-gray-50 disabled:opacity-50 cursor-pointer transition-colors"
            >
              {actionRoomId === myWaitingRoom.id ? "처리 중..." : "방 나가기"}
            </button>
          </div>
        ) : loading ? (
          <div className="flex justify-center py-16">
            <span className="text-gray-500">목록 불러오는 중...</span>
          </div>
        ) : (
          <>
            <div className="flex items-center justify-between mb-6">
              <h1 className="text-xl font-bold text-gray-900">매칭방 목록</h1>
              <button
                type="button"
                onClick={() => setShowCreateModal(true)}
                className="py-2.5 px-5 rounded-xl text-sm font-semibold bg-teal-500 text-white hover:bg-teal-600 cursor-pointer transition-colors flex items-center gap-2"
              >
                <i className="ri-add-line text-lg" aria-hidden />방 만들기
              </button>
            </div>
            {rooms.length === 0 ? (
              <div className="bg-white rounded-2xl border border-gray-200 p-12 text-center">
                <p className="text-gray-500">진행 중인 매칭방이 없습니다.</p>
                <p className="text-sm text-gray-400 mt-2">
                  새 매칭방이 생성되면 여기에 표시됩니다.
                </p>
                <button
                  type="button"
                  onClick={() => setShowCreateModal(true)}
                  className="mt-4 py-2.5 px-5 rounded-xl text-sm font-semibold bg-teal-500 text-white hover:bg-teal-600 cursor-pointer"
                >
                  방 만들기
                </button>
              </div>
            ) : (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                {rooms.map((room) => {
                  const full = isFull(room);
                  const busy = actionRoomId === room.id;
                  return (
                    <div
                      key={room.id}
                      className="bg-white rounded-2xl border border-gray-200 shadow-sm overflow-hidden"
                    >
                      <div className="p-5 border-b border-gray-100">
                        <h2 className="text-lg font-bold text-gray-900 truncate">
                          {room.name}
                        </h2>
                        <p className="text-sm text-gray-500 mt-1">
                          멤버 {room.memberCount}/{room.capacity}명
                          {room.status === "full" && (
                            <span className="ml-2 text-teal-600 font-medium">
                              · 모의투자 시작 가능
                            </span>
                          )}
                          {room.status === "started" && (
                            <span className="ml-2 text-gray-500">
                              · 진행 중
                            </span>
                          )}
                        </p>
                      </div>

                      <div className="p-5">
                        <p className="text-xs font-medium text-gray-500 mb-3">
                          멤버
                        </p>
                        <ul className="space-y-3">
                          {room.members.map((member) => (
                            <li
                              key={member.userId}
                              className="flex items-center gap-3"
                            >
                              <span className="w-10 h-10 rounded-full bg-teal-500 text-white flex items-center justify-center text-sm font-semibold shrink-0" aria-hidden>
                                {member.nickname.charAt(0)}
                              </span>
                              <div className="min-w-0 flex-1">
                                <p className="text-sm font-medium text-gray-900 truncate">
                                  {member.nickname}
                                </p>
                              </div>
                            </li>
                          ))}
                          {room.members.length < room.capacity &&
                            Array.from({
                              length: room.capacity - room.members.length,
                            }).map((_, i) => (
                              <li
                                key={`empty-${i}`}
                                className="flex items-center gap-3"
                              >
                                <div className="w-10 h-10 rounded-full bg-gray-100 border-2 border-dashed border-gray-200 flex-shrink-0 flex items-center justify-center">
                                  <span className="text-xs text-gray-400">
                                    빈 자리
                                  </span>
                                </div>
                                <p className="text-sm text-gray-400">대기 중</p>
                              </li>
                            ))}
                        </ul>
                      </div>

                      <div className="p-5 pt-0 flex gap-2">
                        {room.status === "started" ? (
                          <button
                            type="button"
                            disabled
                            className="flex-1 py-2.5 rounded-xl text-sm font-medium bg-gray-100 text-gray-500 cursor-not-allowed"
                          >
                            진행 중
                          </button>
                        ) : full ? (
                          isInRoom(room) ? (
                            <>
                              <button
                                type="button"
                                onClick={() => handleStart(room.id)}
                                disabled={busy}
                                className="flex-1 py-2.5 rounded-xl text-sm font-bold bg-teal-500 text-white hover:bg-teal-600 disabled:opacity-50 cursor-pointer transition-colors"
                              >
                                {busy ? "처리 중..." : "모의투자 시작"}
                              </button>
                              <button
                                type="button"
                                onClick={() => openLeaveConfirm(room.id)}
                                disabled={busy}
                                className="py-2.5 px-4 rounded-xl text-sm font-medium border border-gray-300 text-gray-700 hover:bg-gray-50 disabled:opacity-50 cursor-pointer transition-colors"
                              >
                                나가기
                              </button>
                            </>
                          ) : (
                            <button
                              type="button"
                              disabled
                              className="w-full py-2.5 rounded-xl text-sm font-medium bg-gray-100 text-gray-400 cursor-not-allowed"
                              title="방이 가득 찼습니다"
                            >
                              참가 불가 (3/3)
                            </button>
                          )
                        ) : (
                          <button
                            type="button"
                            onClick={() => handleJoin(room.id)}
                            disabled={busy}
                            className="w-full py-2.5 rounded-xl text-sm font-bold bg-teal-500 text-white hover:bg-teal-600 disabled:opacity-50 cursor-pointer transition-colors"
                          >
                            {busy ? "처리 중..." : "참가하기"}
                          </button>
                        )}
                      </div>
                    </div>
                  );
                })}
              </div>
            )}
          </>
        )}

        {/* 방 나가기 확인 모달 */}
        {leaveConfirmRoomId && (
          <div className="fixed inset-0 bg-black/50 z-50 flex items-center justify-center p-4">
            <div className="bg-white rounded-2xl shadow-xl w-full max-w-md p-6">
              <div className="w-12 h-12 rounded-full bg-amber-100 flex items-center justify-center mx-auto mb-4">
                <i
                  className="ri-error-warning-line text-2xl text-amber-600"
                  aria-hidden
                />
              </div>
              <h3 className="text-lg font-bold text-gray-900 text-center mb-2">
                방에서 나가시겠습니까?
              </h3>
              <p className="text-sm text-gray-500 text-center mb-6">
                나가시면 해당 방의 팀원 매칭에서 제외됩니다. 다시 참가하려면
                방에 새로 참가해야 합니다.
              </p>
              <div className="flex gap-3">
                <button
                  type="button"
                  onClick={closeLeaveConfirm}
                  className="flex-1 py-2.5 rounded-xl text-sm font-medium border border-gray-300 text-gray-700 hover:bg-gray-50 cursor-pointer"
                >
                  취소
                </button>
                <button
                  type="button"
                  onClick={() => handleLeave(leaveConfirmRoomId)}
                  disabled={actionRoomId === leaveConfirmRoomId}
                  className="flex-1 py-2.5 rounded-xl text-sm font-semibold bg-amber-500 text-white hover:bg-amber-600 disabled:opacity-50 cursor-pointer"
                >
                  {actionRoomId === leaveConfirmRoomId
                    ? "처리 중..."
                    : "나가기"}
                </button>
              </div>
            </div>
          </div>
        )}

        {/* 모의투자방 만들기 모달 */}
        {showCreateModal && (
          <div className="fixed inset-0 bg-black/50 z-50 flex items-center justify-center p-4">
            <div className="bg-white rounded-2xl shadow-xl w-full max-w-md p-6">
              <h3 className="text-lg font-bold text-gray-900 mb-4">
                모의투자방 만들기
              </h3>
              <input
                type="text"
                value={newRoomName}
                onChange={(e) => setNewRoomName(e.target.value)}
                placeholder="방 이름 (선택)"
                className="w-full px-4 py-3 border border-gray-300 rounded-xl text-sm focus:outline-none focus:ring-2 focus:ring-teal-500 mb-4"
              />
              <p className="text-xs text-gray-500 mb-4">
                비우면 자동으로 날짜 기반 이름이 붙습니다. 정원 3명이 모이면
                모의투자를 시작할 수 있습니다.
              </p>
              <div className="flex gap-3">
                <button
                  type="button"
                  onClick={() => {
                    setShowCreateModal(false);
                    setNewRoomName("");
                  }}
                  className="flex-1 py-2.5 rounded-xl text-sm font-medium border border-gray-300 text-gray-700 hover:bg-gray-50 cursor-pointer"
                >
                  취소
                </button>
                <button
                  type="button"
                  onClick={handleCreateRoom}
                  disabled={creating}
                  className="flex-1 py-2.5 rounded-xl text-sm font-semibold bg-teal-500 text-white hover:bg-teal-600 disabled:opacity-50 cursor-pointer"
                >
                  {creating ? "만드는 중..." : "만들기"}
                </button>
              </div>
            </div>
          </div>
        )}
      </main>
    </div>
  );
}
